.QuestionBlock {}


  .QuestionBlock__header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-bottom: 0;
  }

  
    .QuestionBlock__header-arrow {
      -webkit-transform: rotate(180deg);
          -ms-transform: rotate(180deg);
              transform: rotate(180deg);
      -webkit-transition: all 250ms ease;
      -o-transition: all 250ms ease;
      transition: all 250ms ease;
      opacity: 0.25;
    }

    .QuestionBlock__header:hover .QuestionBlock__header-arrow {
      opacity: 1;
    }

    .QuestionBlock.is-collapsed .QuestionBlock__header-arrow {
      -webkit-transform: none;
          -ms-transform: none;
              transform: none;
    }


  .QuestionBlock__questions {
    margin-top: 1em;
  }

  .QuestionBlock.is-collapsed .QuestionBlock__questions {
    display: none;
  }



