.App {
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  padding: 40px;
  color: white;
  text-align: center;
}

.App-title {
  font-size: 1.5em;
  margin-bottom: 0;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}







.Main {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}







label {
  display: block;
}


.Questions {
  -ms-flex: 1 1 35%;
      flex: 1 1 35%;
  padding: 40px;
}

.QuestionBlock {
  border-radius: 5px;
  overflow: hidden;
  -webkit-box-shadow: 0 14px 28px rgba(0, 0, 0, 0.1), 0 0px 10px rgba(0, 0, 0, 0.05);
          box-shadow: 0 14px 28px rgba(0, 0, 0, 0.1), 0 0px 10px rgba(0, 0, 0, 0.05);
  margin-bottom: 20px;
  padding: 20px;
}

h1, h2, h3, h4, h5, h6, p { margin-top: 0; }


input + label {
  margin-top: 20px;
}





